import Glide from '@glidejs/glide';
import MicroModal from 'micromodal';
import IMask from 'imask';

const glideIntro = document.querySelector('.glide--intro');
if (glideIntro) {
  new Glide('.glide--intro', {
    type: 'carousel',
    autoplay: 2000,
    bound: true,
    startAt: 0,
    perView: 1,
    gap: 0

  }).mount();
}
const glideFeedback = document.querySelector('.glide--feedback');
if (glideFeedback) {
  new Glide('.glide--feedback', {
    type: 'carousel',
    autoplay: 3000,
    bound: true,
    startAt: 0,
    perView: 2,
    gap: 16,
    breakpoints: {
      768: {
        perView: 1,
        gap: 0,
        focusAt: 'center',
      }
    }
  }).mount();
}
const glideProjects = document.querySelector('.glide--projects-slider');
if (glideProjects) {
  new Glide('.glide--projects-slider', {
    type: 'carousel',
    autoplay: 3000,
    bound: true,
    startAt: 0,
    perView: 3,
    gap: 16,
    breakpoints: {
      768: {
        perView: 2,
      },
      425: {
        perView: 1,
      }
    }
  }).mount();
}

// header animation

const header = document.querySelector('.header--homepage');
if (header) {
  let lastKnownScrollPosition = 0;
  window.addEventListener('scroll', function (e) {
    lastKnownScrollPosition = window.scrollY;
    if (lastKnownScrollPosition > 70) {
      header.classList.add('header--scrolled');
    } else {
      header.classList.remove('header--scrolled');
    }
  });
}


// for mobile menu
const menuBtn = document.querySelector('.hamburger__icon');
if (menuBtn) {
  let menuOpen = false;
  const menuNav = document.querySelector('.menu');
  const header = document.querySelector('.header');
  menuBtn.addEventListener('click', function (e) {
    e.preventDefault();
    e.stopPropagation();

    if (menuOpen) {
      this.classList.remove('hamburger__icon--open');
      menuNav.classList.remove('menu--open');
      header.classList.remove('header--open');
      document.body.classList.remove('open');
    } else {
      this.classList.add('hamburger__icon--open');
      menuNav.classList.add('menu--open');
      header.classList.add('header--open');
      document.body.classList.add('open');
    }
    menuOpen = !menuOpen;
  });
}

// category filter
const filterWrapper = document.querySelectorAll('.filter');
if (filterWrapper && filterWrapper.length > 0) {
  filterWrapper.forEach(filter => {
    const filterListItems = filter.querySelectorAll('.filter__item');
    filterListItems.forEach(filterItem => {
      filterItem.addEventListener('click', (e) => {
        e.preventDefault();
        const showAllFilter = e.target.hasAttribute('data-category-all');
        const showCategoryFilter = e.target.hasAttribute('data-category');
        const productsList = filter.querySelectorAll('.filter__product');
        toggleFilterClasses(filterListItems, e);
        if (showAllFilter) {
          showAllProducts(productsList);
        } else if (showCategoryFilter) {
          const filterValue = e.target.getAttribute('data-category');
          showFilteredProducts(productsList, filterValue)
        }
      })
    });

    const toggleFilterClasses = (list, e) => {
      list.forEach(item => {
        item.classList.remove('filter__item--active');
      });
      e.target.classList.add('filter__item--active');
    };

    const showAllProducts = (list) => {
      list.forEach(item => {
        item.classList.remove('filter__product--hidden');
      });
    };

    const showFilteredProducts = (list, category) => {
      list.forEach(item => {
        item.getAttribute('data-belongs-to') === category ? item.classList.remove('filter__product--hidden') : item.classList.add('filter__product--hidden');
      });
    };
  });
}



// // comparison
const comparisonBlock = document.querySelectorAll(".comparison");
if (comparisonBlock && comparisonBlock.length > 0) {
  comparisonBlock.forEach(element => {
    const comparisonDivisor = element.querySelector(".comparison__divisor");
    const comparisonHandle = element.querySelector(".comparison__handle");
    const comparisonRange = element.querySelector(".comparison__input");

    comparisonRange.oninput = () => {
      comparisonHandle.style.left = comparisonRange.value + "%";
      comparisonDivisor.style.width = comparisonRange.value + "%";
    }
  });

}


// micromodal
MicroModal.init();
const micromodalLinks = document.querySelectorAll('a[data-micromodal-trigger]');
micromodalLinks.forEach((item) => {
  item.addEventListener('click', (e) => {
    e.preventDefault();
  })
});

// validation for form
const phoneInput = document.querySelector('#user-phone');
if (phoneInput) {

  const phoneMask = IMask(
    document.getElementById('user-phone'), {
      mask: '+{7}(000)000-00-00'
    });
}

// scroll to top


const scrollToTop = document.querySelector('.scroll-top');
if (scrollToTop) {
  scrollToTop.addEventListener('click', (e) => {
    e.preventDefault();
    e.stopPropagation();
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  });

  let lastKnownScrollPosition = 0;
  window.addEventListener('scroll', function (e) {
    lastKnownScrollPosition = window.scrollY;
    if (lastKnownScrollPosition < 100) {
      scrollToTop.classList.add('scroll-top--hidden');
    } else {
      scrollToTop.classList.remove('scroll-top--hidden');
    }
  });
}
